.star-image {
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
  background-position: center;
  background-color: hsl(210deg 19% 19%);
  border-radius: 50%;
}

.thanks-image {
  background-repeat: no-repeat;
  background-position: center;
  width: 200px;
  height: 140px;
  margin: auto;
}